import React, { createContext, useContext, useState, useEffect } from 'react';

type Language = 'en' | 'am';

interface LanguageContextType {
  language: Language;
  setLanguage: (lang: Language) => void;
  t: (key: string) => string;
}

const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

const translations: Record<string, Record<Language, string>> = {
  'home': { en: 'Home', am: '' },
  'about': { en: 'About Us', am: '' },
  'programs': { en: 'Programs & Initiatives', am: '  ' },
  'programs_only': { en: 'Programs', am: '' },
  'initiatives': { en: 'Initiatives', am: '' },
  'resources': { en: 'Resources', am: '' },
  'news': { en: 'News & Events', am: '  ' },
  'news_only': { en: 'News', am: '' },
  'events': { en: 'Events', am: '' },
  'member_events': { en: 'Member Events', am: ' ' },
  'contact': { en: 'Contact Us', am: '' },
  'gallery': { en: 'Gallery', am: ' ' },
  'photos': { en: 'Photos', am: '' },
  'videos': { en: 'Videos', am: '' },
  'members': { en: 'Members', am: '' },
  'members_login': { en: 'Members Login', am: ' ' },
  'register': { en: 'Register', am: '' },
  'archive': { en: 'Archive', am: '' },
  'search': { en: 'Search', am: '' },
  'download': { en: 'Download', am: '' },
  'all_files': { en: 'All Files', am: ' ' },
  'name': { en: 'Name', am: '' },
  'date_modified': { en: 'Date Modified', am: ' ' },
  'type': { en: 'Type', am: '' },
  'size': { en: 'Size', am: '' },
  'files': { en: 'files', am: '' },
  'empty_folder': { en: 'This folder is empty', am: '   ' },
  'loading': { en: 'Loading...', am: ' ...' },
  'read_more': { en: 'Read More', am: ' ' },
};

export const LanguageProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [language, setLanguageState] = useState<Language>(() => {
    const saved = localStorage.getItem('language');
    return (saved as Language) || 'en';
  });

  useEffect(() => {
    localStorage.setItem('language', language);
  }, [language]);

  const setLanguage = (lang: Language) => {
    setLanguageState(lang);
  };

  const t = (key: string): string => {
    return translations[key]?.[language] || key;
  };

  return (
    <LanguageContext.Provider value={{ language, setLanguage, t }}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (!context) {
    throw new Error('useLanguage must be used within LanguageProvider');
  }
  return context;
};

