import React, { useEffect, useState } from 'react';
import { useLanguage } from '../context/LanguageContext';
import { cmsAPI, Page } from '../api/cms';
import {
  buildFallbackPage,
  cachePage,
  getCachedPage,
  DEFAULT_INITIATIVES_CONTENT,
  isContentNewer,
} from '../utils/pageCache';
import './Programs.css';

const Initiatives: React.FC = () => {
  const { language } = useLanguage();
  const defaultFallback = buildFallbackPage({
    pageType: 'initiatives',
    ...DEFAULT_INITIATIVES_CONTENT,
  });

  // Initialize with default fallback, then check cache
  const [page, setPage] = useState<Page>(() => {
    const cached = getCachedPage('initiatives');
    return cached || defaultFallback;
  });

  useEffect(() => {
    // Background fetch to check for admin updates
    const loadPage = async () => {
      try {
        const data = await cmsAPI.getPage('initiatives');
        // Only update if admin has made changes (newer timestamp)
        const cached = getCachedPage('initiatives');
        if (isContentNewer(cached, data)) {
          cachePage('initiatives', data);
          setPage(data);
        } else if (!cached) {
          // If no cache exists and backend returned content, cache it
          cachePage('initiatives', data);
          setPage(data);
        }
      } catch (error) {
        // Silently fail - keep showing default/cached content
        console.error('Failed to load initiatives page:', error);
      }
    };
    loadPage();
  }, []);

  const title = language === 'am' ? '' : 'Initiatives';
  const content = language === 'am' && page?.content_am ? page.content_am : page?.content_en || '';

  return (
    <div className="programs-page">
      <div className="container">
        <div className="content-section">
          <h1>{title}</h1>
          <div
            className="content"
            dangerouslySetInnerHTML={{ __html: content }}
          />
          {page?.sections && page.sections.length > 0 && (
            <div className="sections">
              {page.sections.map((section: any) => (
                <div key={section.id} className="section">
                  {section.image && (
                    <img src={section.image.image} alt={section.image.alt_text} />
                  )}
                  <h2>{section.title}</h2>
                  <div dangerouslySetInnerHTML={{ __html: section.content }} />
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Initiatives;

