// Timestamp comparison functions
export const isContentNewer = (cached: Page | null, fresh: Page | null): boolean => {
  if (!cached || !fresh) return false;
  if (!cached.updated_at || !fresh.updated_at) return false;
  
  const cachedTime = new Date(cached.updated_at).getTime();
  const freshTime = new Date(fresh.updated_at).getTime();
  
  return freshTime > cachedTime;
};

export const isContactInfoNewer = (cached: ContactInfo | null, fresh: ContactInfo | null): boolean => {
  if (!cached || !fresh) return false;
  if (!cached.updated_at || !fresh.updated_at) return false;
  
  const cachedTime = new Date(cached.updated_at).getTime();
  const freshTime = new Date(fresh.updated_at).getTime();
  
  return freshTime > cachedTime;
};

