import React, { useState, useEffect } from 'react';
import { useLanguage } from '../context/LanguageContext';
import { cmsAPI, Page, ContactInfo } from '../api/cms';
import { messagesAPI } from '../api/messages';
import { useToast } from '../context/ToastContext';
import {
  buildFallbackContactInfo,
  buildFallbackPage,
  cacheContactInfo,
  cachePage,
  getCachedContactInfo,
  getCachedPage,
  DEFAULT_CONTACT_CONTENT,
  isContentNewer,
  isContactInfoNewer,
} from '../utils/pageCache';
import './Contact.css';

const Contact: React.FC = () => {
  const { language } = useLanguage();
  const { showSuccess, showError } = useToast();
  const defaultFallbackPage = buildFallbackPage({
    pageType: 'contact',
    ...DEFAULT_CONTACT_CONTENT,
  });
  const fallbackContactInfo = buildFallbackContactInfo();

  // Initialize with default fallback, then check cache
  const [page, setPage] = useState<Page>(() => {
    const cached = getCachedPage('contact');
    return cached || defaultFallbackPage;
  });

  const [contactInfo, setContactInfo] = useState<ContactInfo>(() => {
    const cached = getCachedContactInfo();
    return cached || fallbackContactInfo;
  });

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });
  const [submitted, setSubmitted] = useState(false);
  const [submitting, setSubmitting] = useState(false);

  useEffect(() => {
    // Background fetch to check for admin updates
    const loadData = async () => {
      // Load page content
      try {
        const pageData = await cmsAPI.getPage('contact');
        const cached = getCachedPage('contact');
        if (isContentNewer(cached, pageData)) {
          cachePage('contact', pageData);
          setPage(pageData);
        } else if (!cached) {
          // If no cache exists and backend returned content, cache it
          cachePage('contact', pageData);
          setPage(pageData);
        }
      } catch (error: any) {
        // Silently fail - keep showing default/cached content
        console.error('Failed to load contact page:', error);
      }

      // Load contact info
      try {
        const contactData = await cmsAPI.getContactInfo();
        const cached = getCachedContactInfo();
        if (isContactInfoNewer(cached, contactData)) {
          cacheContactInfo(contactData);
          setContactInfo(contactData);
        } else if (!cached) {
          // If no cache exists and backend returned content, cache it
          cacheContactInfo(contactData);
          setContactInfo(contactData);
        }
      } catch (error: any) {
        // Silently fail - keep showing default/cached content
        console.error('Failed to load contact info:', error);
      }
    };
    loadData();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitting(true);
    try {
      await messagesAPI.submitContact(formData);
      setSubmitted(true);
      setFormData({
        name: '',
        email: '',
        subject: '',
        message: '',
      });
      showSuccess(language === 'am' ? ' !' : 'Message sent successfully!');
      setTimeout(() => setSubmitted(false), 5000);
    } catch (error: any) {
      console.error('Failed to submit contact form:', error);
      const networkIssue = !error.response;
      const maintenanceMessage =
        language === 'am'
          ? '      '
          : 'We are updating our systems right now. Please try again shortly or reach us directly by email.';
      showError(
        error.response?.data?.detail || 
        (networkIssue
          ? maintenanceMessage
          : language === 'am'
            ? '   '
            : 'Failed to send message. Please try again.')
      );
    } finally {
      setSubmitting(false);
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const title = language === 'am' && page?.title_am ? page.title_am : page?.title_en || 'Contact Us';
  const content = language === 'am' && page?.content_am ? page.content_am : page?.content_en || '';

  return (
    <div className="contact-page">
      <div className="container">
        <h1>{title}</h1>
        <div className={`contact-content ${!content ? 'no-cms-content' : ''}`}>
          {content && (
            <div 
              className="contact-cms-content"
              dangerouslySetInnerHTML={{ __html: content }}
            />
          )}
          <div className="contact-right-column">
          <div className="contact-info">
            <h2>{language === 'am' ? ' ' : 'Contact Information'}</h2>
            <p>
                {contactInfo 
                  ? (language === 'am' ? contactInfo.description_am : contactInfo.description_en) || 
                    (language === 'am'
                      ? '     '
                      : 'Please feel free to contact us for any inquiries or information.')
                  : (language === 'am'
                ? '     '
                      : 'Please feel free to contact us for any inquiries or information.')
                }
            </p>
            <div className="info-item">
                <strong>Email:</strong> {contactInfo?.email || 'caduarduet@gmail.com'}
              </div>
              {contactInfo?.phone && (
                <div className="info-item">
                  <strong>Phone:</strong> {contactInfo.phone}
            </div>
              )}
            <div className="info-item">
                <strong>Address:</strong> {contactInfo 
                  ? (language === 'am' && contactInfo.address_am ? contactInfo.address_am : contactInfo.address_en)
                  : 'Addis Ababa, Ethiopia'}
            </div>
          </div>
          <form className="contact-form" onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="name">
                {language === 'am' ? '' : 'Name'}
              </label>
              <input
                type="text"
                id="name"
                name="name"
                value={formData.name}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="email">
                {language === 'am' ? '' : 'Email'}
              </label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="subject">
                {language === 'am' ? '' : 'Subject'}
              </label>
              <input
                type="text"
                id="subject"
                name="subject"
                value={formData.subject}
                onChange={handleChange}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="message">
                {language === 'am' ? '' : 'Message'}
              </label>
              <textarea
                id="message"
                name="message"
                value={formData.message}
                onChange={handleChange}
                rows={6}
                required
              />
            </div>
            <button type="submit" className="submit-btn" disabled={submitting}>
              {submitting 
                ? (language === 'am' ? '...' : 'Sending...') 
                : (language === 'am' ? '' : 'Send')
              }
            </button>
            {submitted && (
              <div className="success-message">
                {language === 'am' ? ' !' : 'Message sent!'}
              </div>
            )}
          </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;

